#circle {
  transform: scale(1);
  transform-box: fill-box;
  transform-origin: center;
  animation: circle .8s linear 1s infinite forwards;
}

@keyframes circle {
  to {
    transform: scale(1.5);
  }
  
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

/* 반응형 */
@media (max-width: 1700px) {
  .charts_box div {
    width: 180% !important;
  }

  .charts_box svg {
    width: 180% !important;
    margin-left: -40px;
  }
}

@media (max-width: 1580px) {
  .part_status_box{margin: 10px 0;}
  .charts_box div {
    width: 190% !important;
  }

  .charts_box svg {
    width: 190% !important;
    margin-left: -40px;
  }
} 

@media (max-width: 1500px) {
  .cond_status {
    margin-left: 5px;
    grid-template-columns: unset;
    grid-template-rows: 1fr 240px 200px;
  }
  .charts_box div {
    width: 140% !important;
  }
  .charts_box svg {
    width: 100% !important;
    margin-left: -10%;
  }
} 

@media (max-width: 1300px) {
  .chart_tab {
    flex-wrap: wrap;
  }
  .chart_tab > div {
    width: 50%;
  }
  .chart_tab > div:nth-child(3) {
      width: 100%;
  }
} 

@media (min-width: 992px){
  .navbar-expand-lg .navbar-collapse {
      justify-content: space-between;
  }
}

@media (max-width: 990px) {
  .main_left, .sub_left{padding-right: 0;}
  .map_box {
    display: none;
  }
  .sub_left {
    margin: 0 auto;
    width: 99%;  
  }
  .sub_right, .main_right {
    margin: 0 auto;
    width: 99%;
  }
  .main_right {
    padding-left: 0;
  }
  .cond_status {
    margin-left: 0;
  }
  .part_status th {
    font-size: 0.75rem !important;
  }
  .main_table {
    margin-left: 0;
  }
  .mobile_top {
    margin-top: 10px;
  }
  .desktop_serach {
    display: none;
  }
  .mobile_serach {
    display: block !important;
  }
  .mro_search{display: block;}
  .mro_m_left{width:100%;}
  .mro_m_right{width:100%;}

  .maintenance .main_left{width: 100%;}
  .navbar-nav .MuiButtonBase-root{width: 100%;}
  .navbar-nav .nav-link{width: 100%;}
}

